@import url(https://fonts.googleapis.com/css?family=Roboto);
/* TODO: Create anim classes and import to css where used */

@-webkit-keyframes anim-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes anim-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes anim-scale-up {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.5) translateY(-10px);
            transform: scale(1.5) translateY(-10px);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes anim-scale-up {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.5) translateY(-10px);
            transform: scale(1.5) translateY(-10px);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes anim-rotate-scale-up {
  0% {
    -webkit-transform: scale(1) rotateZ(0);
            transform: scale(1) rotateZ(0);
  }
  50% {
    -webkit-transform: scale(2) rotateZ(180deg);
            transform: scale(2) rotateZ(180deg);
  }
  100% {
    -webkit-transform: scale(1) rotateZ(360deg);
            transform: scale(1) rotateZ(360deg);
  }
}

@keyframes anim-rotate-scale-up {
  0% {
    -webkit-transform: scale(1) rotateZ(0);
            transform: scale(1) rotateZ(0);
  }
  50% {
    -webkit-transform: scale(2) rotateZ(180deg);
            transform: scale(2) rotateZ(180deg);
  }
  100% {
    -webkit-transform: scale(1) rotateZ(360deg);
            transform: scale(1) rotateZ(360deg);
  }
}

/* Borders and text */

/* responsive breakpoints */
/*@value sm: 600px;*/
/*@value md: 600px;*/
/*@value lg: 720px;*/

/*@value gutterwidth: 10%;*/

@font-face {
  font-family: 'Bebas';
  src: local('Bebas'), url(/bhishp-cv/static/media/BEBAS.7d3d597a.ttf) format('truetype');
}

html {
  font-size: 11.5px;
}
h2 {
  font-size: 1.4rem;
}
/* responsive queries */
@media all and (min-width: 600px) {
  html { font-size: 15px; }
  h2 { font-size: 1.5rem; }
}
@media all and (min-width: 720px) {
  html { font-size: 16px; }
}

body {
  background-color: #fff;
  color: #222222;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
}

.CV {
  background-color: #595959;
  padding: 0;
}

.CVWrapper {
  margin: 0 auto;
  max-width: 800px;
}

.CVContent {
  background: -webkit-gradient(linear, left top, right top, color-stop(33%, #e0e0df), color-stop(33%, #e0e0df), color-stop(33%, #fff) );
  background: -webkit-linear-gradient(left, #e0e0df 33%, #e0e0df 33%, #fff 33% );
  background: linear-gradient(to right, #e0e0df 33%, #e0e0df 33%, #fff 33% );
}

.CVFooter {
  background-color: #2c3235;
  color: #fff;
  min-height: 80px;
  padding: 5px 14px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.CVHeader {
  color: #fff;
  display: flex;
  align-items: center;
  position: relative;
  background-color: #2c3235;
  height: 200px;
}

.CVHeader-corner-box {
  position: absolute;
  top: -4px;
  right: 13.5px;
  font-size: 1rem;
  text-align: right;
}

@media all and (min-width: 600px) {
  .CVHeader-corner-box { font-size: 0.9rem; }
}

.CVHeader-headshot-wrapper {
  align-self: center;
  width: 35%;
  text-align: center;
}
@media all and (min-width: 600px) {
  .CVHeader-headshot-wrapper { width: 25%; }
}
.CVHeader-headshot {
  border-radius: 50%;
  width: 50%;
  min-width: 100px;
  max-width: 115px;
  -webkit-animation: anim-spin infinite 20s linear;
          animation: anim-spin infinite 20s linear;
}

.CVHeader-headings {
  width: 50%;
  font-family: 'Bebas', courier, 'Roboto', sans-serif;
  text-transform: uppercase;
}
.CVHeader-headings * {
  margin: 0;
}

.CVHeader-name {
  font-size: 3.125rem;
  margin-bottom: -3px;
}
.CVHeader-forename {
  color: #fff;
}
.CVHeader-surname {
  color: #45A2DA;
  margin-left: 5px;
}

.CVHeader-profession {
  font-size: 1.8125rem;
  color: #fff;
}
.CVHeader-profession span:nth-child(even) {
  color: #45A2DA;
  margin-left: 5px;
}

.CVHeader-personal-information {
  color: #fff;
  width: 25%;
  list-style-type: none;
  padding-left: 0;
  white-space: nowrap;
}
.CVHeader-personal-information a:link, a:visited, a:hover, a:active {
  color: #fff;
  text-decoration: none;
}
@media all and (max-width: 600px) {
  .CVHeader-personal-information {
    position: absolute;
    bottom: 0;
    right: 6px;
  }
}

.Section {
  padding-top: 10px;
}

.Section-header {
  display: flex;
  align-items: center;
}
.Section-heading {
  width: 33%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 0;
  /*background-color: quillgray;*/
}
.Section-heading h2 {
  font-family: 'Bebas', courier, 'Roboto', sans-serif;
  text-transform: uppercase;
  margin: 0;
}
.Section-divider {
  width: 63%;
  margin: 0 auto;
  border: 0;
  border-top: 2px solid black;
}

.Article {
  padding: 10px 0;
}
.Article-wrapper {
  display: flex;
}
.Article-aside {
  width: 33%;
  /*background-color: quillgray;*/
  text-align: end;
  padding: 0 20px;
}
.Article-info {
  width: 67%;
  padding: 0 25px;
}

/* aside */
.Article-aside-title {
  font-family: 'Bebas', courier, 'Roboto', sans-serif;
  text-transform: uppercase;
  color: #45A2DA;
  word-spacing: 0.2em;
  font-size: 16px;
  font-weight: bold;
}

/* info */
.Article-header {
  display: flex;
  align-items: center;
}
.Article-header-icons {
  list-style-type: none;
  padding: 0;
  margin: 0 0 0 6px;
}
.Article-header-icons > li {
  float: left;
}
.Article-header-icons > li > img {
  width: 35px;
  border-radius: 25%;
}

.Article-header h3 {
  text-transform: uppercase;
  font-size: 22px;
  margin-top: 0;
  margin-bottom: 0;
}
.Article-body {
  display: flex;
}
.Article-copy {
  width: 90%;
}
.Article-footnotes {
  font-size: 0.75rem;
}
.Article-icons {
  width: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0 0 5% 0;

  list-style-type: none;
}
.Article-icons li > img {
  width: 1.9rem;
}

.CVIcon {
  width: 1.375rem !important;
  height: 1.375rem !important;
  padding: 8px;
  margin-right: 5px;
  background-color: #45A2DA;
  border-radius: 50%;
  color: white;
}
.CVIcon:hover {
  -webkit-animation: anim-scale-up 0.9s ease-in-out;
          animation: anim-scale-up 0.9s ease-in-out;
}




.HeadingIcon:hover {
  -webkit-animation: anim-rotate-scale-up 0.9s ease-in-out;
          animation: anim-rotate-scale-up 0.9s ease-in-out;
}

.TechIcon:hover {
  -webkit-animation: anim-scale-up 0.9s ease-in-out;
          animation: anim-scale-up 0.9s ease-in-out;
}

